function embedGoogleForm() {
    // Find the embedded iframe
    var iframe = document.createElement('iframe');
    iframe.src = "https://docs.google.com/forms/d/e/1FAIpQLScCDSwUrp-2Ry3ApaksBhhJc1aUwaPfYpH0jWS7mJ7tgz8j7A/viewform?embedded=false";
    iframe.width = "640";
    iframe.height = "1380";
    iframe.frameborder = "0";
    iframe.marginheight = "0";
    iframe.marginwidth = "0";
    iframe.scrolling = "no";


    // Append the iframe to the "embeddedForm" div to render the form
    document.getElementById('embeddedForm').appendChild(iframe);

    // Trigger form submission
    setTimeout(function() {
        iframe.contentWindow.postMessage(JSON.stringify({
            type: 'submit'
        }), '*');
    }, 100);
}

// Check if the form is embedded
var formEmbedded = false;

// Wait for the DOM to load
document.addEventListener('DOMContentLoaded', function() {
    // Check if the form is already embedded
    if (!formEmbedded) {
        // Embed the form when the page loads
        embedGoogleForm();
        formEmbedded = true; // Set the flag to true after embedding the form
    }

});